@import "settings"                                                                                                      // settings
@import "_mixins/mixins_list"                                                                                           // mixins
@import "_parts/_ui.sass"

/* main page */
.why-block
  margin-top: 15px
  @media (min-width: 415px)
    margin-top: 30px
  @media (min-width: 1024px)
    margin-top: 50px
  @media (min-width: 1200px)
    margin-top: 100px
  &__content
    @media (min-width: 768px)
      display: flex
    &__left-side
      background-color: #f6f6f6
      padding: 15px 0
      position: relative
      z-index: 5
      @media (min-width: 768px)
        background-color: transparent
        padding: 0
        margin-top: 5px
      @media (min-width: 1024px)
        flex: 1 1 300px
    &__right-side
      margin-top: 15px
      @media (min-width: 415px)
        margin-top: 20px
      @media (min-width: 768px)
        margin-top: 0
        padding-left: 25px
      @media (min-width: 960px)
        flex: 1 1 calc(100% - 325px)
        padding-left: 50px
  &__image
    display: block
    margin: 0 auto
    @media (min-width: 768px)
      margin: 0
  &__descr
    margin-top: 5px
    font-size: 15px
    line-height: 20px
    @media (min-width: 414px)
      font-size: 16px
    @media (min-width: 768px)
      margin-top: 50px
    @media (min-width: 960px)
      font-size: 19px
      line-height: 22px
    @media (min-width: 1024px)
      margin-top: 70px
      br
        display: none
    p
      display: block
      border-bottom: 1px solid #999
      padding: 6px 0
      @media (min-width: 960px)
        padding: 10px 0
      &:last-child
        border-bottom: none
  &__title
    @media (min-width: 768px)
      display: block
      position: relative
      &:before
        content: ''
        display: block
        width: 85vw
        height: calc(100% + 30px)
        position: absolute
        top: -15px
        right: 0
        background-color: #f2f2f2
        z-index: -1
    @media (min-width: 1200px)
      &:before
        width: calc(100% + 200px)
        height: calc(100% + 60px)
        top: -30px

.about-block
  margin-top: 20px
  @media (min-width: 415px)
    margin-top: 30px
  @media (min-width: 1200px)
    margin-top: 70px
    overflow-x: hidden
  &__row
    display: flex
    flex-wrap: wrap
    @media (min-width: 768px)
      flex-wrap: nowrap
      align-items: stretch
      &:nth-of-type(2n)
        flex-direction: row-reverse
    @media (min-width: 1200px)
      &:nth-of-type(2n)
        .about-block__row__left-side
          &:before
            left: 0
            right: auto
    &__left-side
      order: 2
      flex: 1 1 calc(100% + 30px)
      width: calc(100% + 30px)
      margin: 15px -15px
      background-color: #f6f6f6
      padding: 15px
      @media (min-width: 415px)
        padding: 20px 30px
        margin: 30px -30px
      @media (min-width: 768px)
        order: 1
        flex: 1 1 50%
        margin: 0
        padding: 25px 20px
      @media (min-width: 960px)
        display: flex
        flex-wrap: wrap
        align-items: center
        padding: 30px
      @media (min-width: 1200px)
        position: relative
        &:before
          content: ''
          display: block
          width: 100vw
          height: 100%
          position: absolute
          right: 0
          top: 0
          z-index: -1
          background-color: #f6f6f6
        .descr-block
          max-width: 420px
          margin: 0 auto
    &__right-side
      order: 1
      flex: 1 1 100%
      @media (min-width: 768px)
        flex: 1 1 50%
        order: 2
      img
        display: block
        width: auto
        max-width: 100%
        margin: 0 auto
        @media (min-width: 1200px)
          width: 100%
    &:nth-of-type(3n)
      .about-block__row__left-side
        background-color: $orange-color
        @media (min-width: 1200px)
          &:before
            background-color: $orange-color
    &:last-child
      .about-block__row__left-side
        margin-bottom: 0
  &__title
    font-family: Montserrat, sans-serif
    font-size: 22px
    line-height: 22px
    font-weight: 400
    text-transform: uppercase
    display: inline-block
    margin-bottom: 15px
    letter-spacing: 1px
    @media (min-width: 768px)
      font-size: 24px
      line-height: 24px
    @media (min-width: 960px)
      font-size: 34px
      line-height: 34px
    @media (min-width: 1200px)
      font-size: 42px
      line-height: 42px
    span
      font-size: 18px
      line-height: 18px
      display: block
      margin-top: 10px
      text-transform: none
      @media (min-width: 768px)
        font-size: 20px
        line-height: 20px
      @media (min-width: 960px)
        font-size: 22px
        line-height: 22px
        margin-bottom: 30px
      @media (min-width: 1200px)
        font-size: 25px
        line-height: 25px
  &__descr
    font-size: 15px
    line-height: 22px
    @media (min-width: 414px)
      font-size: 16px
    @media (min-width: 768px)
      font-size: 15px
      line-height: 20px
    @media (min-width: 960px)
      font-size: 18px
      line-height: 24px
    @media (min-width: 1200px)
      font-size: 19px
      line-height: 27px
    p
      margin-bottom: 10px
      @media (min-width: 960px)
        margin-bottom: 20px
      span
        display: inline-block
        font-size: 350%
        line-height: 100%
        float: left
        margin: -6px 3px -5px -5px
        text-align: left
        @media (min-width: 414px)
          margin: -8px 3px -5px -5px
        @media (min-width: 960px)
          font-size: 450%
          margin: -26px 5px -10px -8px
      &:last-child
        margin-bottom: 0
.team-block
  padding: 15px 0
  background-color: #ececec
  position: relative
  z-index: 1
  @media (min-width: 414px)
    padding: 30px 0
  @media (min-width: 768px)
    margin-top: 70px
    position: relative
    &:before
      content: ''
      display: block
      width: calc(100% - 30px)
      height: 100%
      position: absolute
      top: -15px
      left: 15px
      background-color: #f9f9f9
      z-index: -1
  @media (min-width: 1024px)
    margin-top: 100px
  @media (min-width: 1200px)
    background-color: transparent
    margin-top: 170px
    &:before
      display: none
  &__subtitle
    line-height: 24px
    @media (min-width: 415px)
      br
        display: none
    @media (min-width: 960px)
      font-size: 32px
      line-height: 40px
  &__descr
    @media (min-width: 960px)
      font-size: 19px
      line-height: 24px
    ul
      margin: 15px 0
      padding: 0
      list-style-type: none
      li
        position: relative
        display: block
        padding-left: 20px
        margin-bottom: 5px
        @media (min-width: 960px)
          margin-bottom: 10px
        &:last-child
          margin-bottom: 0
        &:before
          content: ''
          display: block
          width: 8px
          height: 8px
          border-radius: 50%
          background-color: $accent-color
          position: absolute
          top: 10px
          left: 0

  &__full-side
    @media (min-width: 768px)
      flex: 1 0 100%
    .team-block__subtitle
      margin: 10px 0
      display: inline-block
      @media (min-width: 768px)
        margin-top: 20px
    p
      font-size: 15px
      @media (min-width: 414px)
        font-size: 16px
      @media (min-width: 768px)
        font-size: 18px
        line-height: 20px
        margin-top: 10px
      @media (min-width: 960px)
        font-size: 20px
    .join-btn
      margin: 15px 0 0
      @media (min-width: 768px)
        max-width: 300px
        margin: 30px auto 0
      @media (min-width: 1200px)
        margin-top: 50px
  &__content
    @media (min-width: 768px)
      display: flex
      flex-wrap: wrap
    @media (min-width: 1200px)
      position: relative
      &:before
        content: ''
        display: block
        width: 95%
        height: 85%
        background-color: #f9f9f9
        position: absolute
        top: -75px
        left: -45px
        z-index: -2
      &:after
        content: ''
        display: block
        width: 95%
        height: calc(100% + 90px)
        background-color: #ececec
        position: absolute
        top: -60px
        left: -60px
        z-index: -1
    &__left-side
      @media (min-width: 768px)
        flex: 1 1 50%
        padding-top: 20px
    &__right-side
      @media (min-width: 768px)
        flex: 1 1 50%
        margin-top: -60px
        position: relative
        &:before
          content: ''
          display: block
          width: 100%
          height: 100%
          position: absolute
          top: -15px
          right: -15px
          background-color: #f9f9f9
          z-index: -1
      @media (min-width: 1200px)
        margin-top: -120px
        right: 15px
      .team-image
        @media (min-width: 415px)
          width: 100%
.info-block
  padding-top: 35px
  min-height: calc(100vh - 70px)
  background-position: bottom center
  background-size: cover
  color: #fff
  @media (min-width: 768px)
    padding-top: 70px
  @media (min-width: 1024px)
    background-position: center
  @media (min-width: 1200px)
    margin-top: -135px
    padding-top: 300px
    min-height: 100vh
  &__title
    text-align: center
    @media (min-width: 768px)
      font-size: 28px
      line-height: 32px
    @media (min-width: 960px)
      font-size: 32px
      line-height: 40px
    @media (min-width: 1200px)
      font-size: 35px
      line-height: 45px
  h3
    font-weight: 400
    text-align: center
    font-size: 15px
    line-height: 20px
    letter-spacing: .5px
    display: block
    margin-top: 15px
    @media (min-width: 768px)
      font-size: 17px
      font-weight: 100
    @media (min-width: 960px)
      font-size: 19px
      line-height: 22px
      margin-top: 30px
    @media (min-width: 1200px)
      font-size: 20px
      line-height: 24px
  .more-info-btn
    margin-top: 30px
    @media (min-width: 768px)
      max-width: 300px
      margin: 30px auto
    @media (min-width: 960px)
      margin-top: 50px

.help-block
  margin: 15px 0
  @media (min-width: 768px)
    margin: 0
    padding: 40px 0
  @media (min-width: 1200px)
    padding: 80px  0
  &__title
    text-align: center
  &__content
    margin-top: 15px
    @media (min-width: 768px)
      display: flex
      align-items: stretch
      justify-content: space-around
    @media (min-width: 1200px)
      margin-top: 40px
    &__item
      margin-bottom: 15px
      padding: 15px 0 0
      &:last-child
        margin-bottom: 0
      .image-block
        width: 100%
        .help-image
          display: block
          margin: 0 auto
      .descr-block
        text-align: center
        max-width: 280px
        margin: 15px auto 0

