.footer
  background-color: $txt-color
  color: #999
  padding: 15px 0
  text-align: center
  @media (min-width: 414px)
    padding: 30px 0 15px
  @media (min-width: 1200px)
    padding: 30px 0 15px
  .logo-link
    color: $accent-color
    display: inline-block
    font-family: Montserrat, sans-serif
    font-weight: 700
    font-size: 18px
    line-height: 24px
    margin: 0 auto 0 0
    @media (min-width: 340px)
      font-size: 20px
    @media (min-width: 360px)
      font-size: 22px
    @media (min-width: 414px)
      font-size: 24px
    @media (min-width: 768px)
      order: 1
      flex: 0 0 auto
    span
      color: #fff
  &__content
    @media (min-width: 768px)
      display: flex
      flex-wrap: wrap
      align-items: flex-start
      justify-content: flex-start
    &__menu
      margin-top: 15px
      @media (min-width: 768px)
        flex: 1 1 100%
        order: 3
        display: flex
        flex-wrap: wrap
        align-items: stretch
        margin: 30px 0
      @media (min-width: 1200px)
        display: flex
        flex: 1 1 auto
        flex-wrap: wrap
        order: 2
        padding: 0 40px
        margin: 0
        justify-content: flex-end
      .menu-column
        padding-bottom: 15px
        border-bottom: 1px solid #222
        margin-bottom: 15px
        @media (min-width: 768px)
          flex: 1 1 50%
          border-bottom: none
          padding: 0
          &:nth-of-type(2n)
            .fmenu-list
              justify-content: flex-end
        @media (min-width: 1200px)
          flex: 0 1 auto
          margin: 0 20px
        &:last-child
          margin-bottom: 0
          padding-bottom: 15px
          @media (min-width: 768px)
            padding-bottom: 0
      .fmenu-list
        margin: 0
        padding: 0
        list-style-type: none
        font-size: 16px
        line-height: 16px
        @media (min-width: 768px)
          display: flex
          flex-wrap: wrap
        @media (min-width: 1200px)
          flex-direction: column
          align-items: flex-start
          justify-content: flex-end
        &__item
          display: block
          text-align: center
          margin-bottom: 10px
          @media (min-width: 768px)
            display: inline-block
            margin-left: 20px
            margin-bottom: 0
            &:nth-of-type(1)
              margin-left: 0
          @media (min-width: 1200px)
            margin: 0 0 10px 0
          &:last-child
            margin-bottom: 0
        &__link
          display: inline-block
          transition: color .3s
          &:hover
            color: #fff
    .phone-link
      color: #fff
      display: inline-block
      margin: 15px 0
      font-size: 22px
      line-height: 22px
      position: relative
      &:after
        content: ''
        display: block
        width: 0
        height: 1px
        background-color: transparent
        position: absolute
        bottom: -5px
        left: 0
        transition: background-color .3s, width .3s
      &:hover
        &:after
          width: 100%
          background-color: $accent-color
      @media (min-width: 768px)
        flex: 0 0 auto
        order: 2
        margin: 0
  &__copyright
    padding: 5px 0
    font-size: 13px
    line-height: 20px
    @media (min-width: 340px)
      font-size: 14px
    @media (min-width: 1200px)
      margin-top: 40px
      text-align: left
  .topBtn
    position: fixed
    bottom: 20px
    right: 20px
    z-index: 5
    span
      display: flex
      align-items: center
      justify-content: center
      width: 50px
      height: 50px
      background-color: rgba(0,0,0,.8)
      border: 1px solid transparent
      color: #fff
      font-size: 20px
      line-height: 20px
      opacity: 1
      transition: opacity .3s, border-color .3s
    &:hover
      span
        border-color: $accent-color
