.btn-border-style
  display: block
  width: 100%
  border: 1px solid $txt-color
  padding: 15px 0
  text-align: center
  color: #fff
  position: relative
  overflow: hidden
  transition: border-color .3s, color .3s
  margin-bottom: 15px
  text-transform: uppercase
  &.white-style
    border-color: #fff
  span
    position: relative
    z-index: 2
  &:before
    content: ''
    display: block
    width: 100%
    height: 100%
    position: absolute
    background-color: $accent-color
    top: 0
    left: 0
    transform: translateY(100%)
    transition: transform .3s
  &:hover
    border-color: $accent-color
    color: #fff
    &:before
      transform: translateY(0)
.section-title
  text-transform: uppercase
  font-family: Montserrat, sans-serif
  font-weight: 400
  font-size: 20px
  line-height: 30px
  @media (min-width: 414px)
    font-size: 22px
  @media (min-width: 768px)
    font-size: 24px
  @media (min-width: 960px)
    font-size: 28px
    line-height: 38px
  @media (min-width: 1024px)
    font-size: 32px
    line-height: 42px
  @media (min-width: 1200px)
    font-size: 35px
    line-height: 45px
.page-title
  font-size: 25px
  line-height: 25px
.product-filters-block
  .list-group-item
    display: block
    border-bottom: 1px solid #eee
    margin-top: 10px
    &:nth-of-type(1)
      margin-top: 0
    &:last-child
      border-bottom: none
    .filter-item-name
      display: flex
      flex-direction: row-reverse
      justify-content: space-between
      .product-filters-title
        font-size: 18px
        line-height: 20px
      .product-filter-switch-icon
        font-size: 20px
        line-height: 20px
        transform: scale(1,1)
        transition: transform .3s
      &.open
        .product-filter-switch-icon
          transform: scale(1,-1)
    .product-filter
      margin: 15px 0
      .form-group
        input
          display: block
          width: 100%
          border: 1px solid #aaa
          border-radius: 0
          padding: 5px 10px
          @include input-placeholder
            opacity: 1
            transition: opacity 0.3s ease
          &:focus
            @include input-placeholder
              opacity: 0
              transition: opacity 0.3s ease
      .product-filters-item-more
        margin-top: 15px
        li.checkbox
          margin-bottom: 5px
          &:last-child
            margin-bottom: 0
          input
            position: absolute
            visibility: hidden
            height: 0px
            width: 0px
            opacity: 0
          label
            display: inline-flex
            align-items: center
            justify-content: flex-start
            position: relative
            &:before
              content: ''
              display: inline-block
              width: 20px
              height: 20px
              border: 1px solid $txt-color
              margin-right: 10px
              transition: background-color .3s, border-color .3s
            &:after
              font-family: Fontello
              font-style: normal
              font-weight: normal
              speak: never
              display: inline-block
              margin: 0 auto
              text-decoration: inherit
              text-align: center
              font-variant: normal
              text-transform: none
              content: '\e809'
              width: 16px
              height: 16px
              position: absolute
              left: 2px
              top: 2px
              font-size: 14px
              line-height: 16px
              color: #fff
              opacity: 0
              transition: opacity .3s
          &.active
            label
              &:before
                background-color: $accent-color
                border-color: $accent-color
              &:after
                opacity: 1
        .product-filters-no-items-found
          &.hidden
            display: none



